import '../styles/aboutHero.css';
import React from 'react';
import { Fade } from 'react-reveal';
import { useState } from 'react';
import image from '../assets/academy.png';

function AboutHero () {
    const Data = [
        {
            addrese : '    4',
        },
        {
            addrese : '    4',
        },
        {
            addrese : '    4',
        },
        {
            addrese : '    4',
        }
    ]
    const [click, setClick] = useState(1);
    const handleClick = (index) => {
        setClick(index)
    }
    return (
        <>
        <div className='alllll'>
            <Fade right>
            <div className='one'>
                <div className='btns-one'>
                    <button onClick={() => handleClick(1)} className={click === 1 ? 'buttonactive' : 'button'}> </button>
                    <button onClick={() => handleClick(2)} className={click === 2 ? 'buttonactive' : 'button'}></button>
                </div>
                <Fade right>
                <div className={click === 1 ? 'oneactive' : 'one1'}>
                    <p className='p'>            .         .                      .                       .                   .               .      .</p>
                </div>
                </Fade>
                <div className={click === 2 ? 'twoactive' : 'two2'}>
                    {Data.map((item, index) => {
                        return (
                            <Fade right>
                            <div className='carddd' key={index}>
                                <p><i class="fa-solid fa-location-dot"></i>{item.addrese}</p>
                            </div>
                            </Fade>
                        )
                    })}
                </div>
            </div>
            </Fade>
            <Fade left>
            <div className='two'>
                <Fade left>
                <h1 className={click === 2 ? 'headeractive' : 'header'}> <span></span></h1>
                </Fade>
                <Fade left>
                <img src={image} className={click === 1 ? 'imageactive' : 'image'}></img>
                </Fade>
            </div>
            </Fade>
        </div>
        </>
    )
}

export default AboutHero;