import '../styles/heroWebCourse.css';
import React from 'react';
import { useState } from 'react';
import { Fade } from 'react-reveal';

function HeroMobileCourse() {
    const DataOnline = [
        {
            h4 : '  59',
            start : '25-6',
            dates : ' ',
            houres : '6-9 ',
        },
        {
            h4 : '  60',
            start : '1-9',
            dates : ' ',
            houres : '6-9 ',
        }
    ];
    const DataOfline = [
        {
            h4 : '  3',
            start : '25-6',
            dates : ' ',
            houres : '6-9 ',
        },
        {
            h4 : '  4',
            start : '1-9',
            dates : ' ',
            houres : '6-9 ',
        }
    ];
    const [toggleState, setToggleState] = useState(1);
    const toggleTap = (index) => {
        setToggleState(index)
    }
    const [click, setClick] = useState(1);
    const handleClick = (index) => {
        setClick(index)
    }
    return (
        <>
        <div className='v'>
            <Fade right>
            <div className='content22'>
                <h1>   </h1>
                <p><i class="fa-solid fa-circle-check"></i>72   </p>
                <p><i class="fa-solid fa-circle-check"></i>     </p>
                <p><i class="fa-solid fa-circle-check"></i>    </p>
                <p><i class="fa-solid fa-circle-check"></i>       </p>
                <p><i class="fa-solid fa-circle-check"></i>     </p>
                <button onClick={() => handleClick(2)}> </button>
            </div>
            </Fade>
            <Fade left>
            <div className='v2'>
                <div className='buttons'>
                    <button 
                    className={toggleState === 1 ? 'button buttonactive' : 'button'}
                    onClick={() => toggleTap(1)}
                    ></button>
                    <button 
                    className={toggleState === 2 ? 'button buttonactive' : 'button'}
                    onClick={() => toggleTap(2)}
                    ></button>
                </div>
                <div className={toggleState === 1 ? 'all-online-active' : 'all-online'}>
                    {DataOnline.map((item, index) => {
                        return (
                            <div className='card-all' key={index}>
                                <h4>{item.h4}</h4>
                                <p> : {item.start}</p>
                                <p> : {item.dates}  : {item.houres}</p>
                            </div>
                        )
                    })}
                </div>
                <div className={toggleState === 2 ? 'all-ofline-active' : 'all-ofline'}>
                    {DataOfline.map((item, index) => {
                            return (
                                <div className='card-all' key={index}>
                                    <h4>{item.h4}</h4>
                                    <p> : {item.start}</p>
                                    <p> : {item.dates}  : {item.houres}</p>
                                </div>
                            )
                        })}
                </div>
            </div>
            </Fade>
        </div>
        <div className={click === 2 ? 'form24' : 'form23'}>
            <i 
            class="fa-regular fa-circle-xmark"
            onClick={() => handleClick(3)}></i>
            <h3>    </h3>
            <div>
                <input type="text" placeholder=" " className='input55' />
                <input type="text" placeholder=" " className='input55' />
                <button type="submit" id="submitBtn"> </button>
            </div>
        </div>
        </>
    )
}

export default HeroMobileCourse;