import '../styles/youllLearnWeb.css';
import React from 'react';
import { Fade } from 'react-reveal';

function YoullLearnWeb() {
    const Data = [
        {
            h3 : '  ',
            p : '   HTML    CSS    JavaScript.',
        },
        {
            h3 : '     YouTube',
            p : '       YouTube.',
        },
        {
            h3 : '  ',
            p : '            .',
        },
        {
            h3 : '  ',
            p : '            Bootstrap 5  Sass  Webpack.',
        },
        {
            h3 : '  ',
            p : '      Sass  Webpack.',
        },
        {
            h3 : '  ',
            p : '          .',
        },
        {
            h3 : '  ',
            p : '           API.',
        },
        {
            h3 : '    Hugo',
            p : '            Hugo.',
        }
    ]
    return (
        <>
        <div className='all55'>
            <Fade>
            <h1>    </h1>
            </Fade>
            <div className='avram-all66'>
                {Data.map((item, index) => {
                    return (
                        <Fade right>
                        <div className='avram-card66' key={index}>
                            <h3><i class="fa-solid fa-circle-check"/>{item.h3}</h3>
                            <p>{item.p}</p>
                        </div>
                        </Fade>
                    )
                })}
            </div>
        </div>
        </>
    )
}

export default YoullLearnWeb;