import '../styles/FAQ.css';
import React from 'react';
import { useState } from 'react';
import { Fade } from 'react-reveal';

function FAQ() {
    const Data = [
        {
            index : 1,
            question : '       ',
            answer : '                      .               .               .                            .                              .                !',
        },
        {
            index : 2,
            question : '       ',
            answer : '                      .               .               .                            .                              .                !',
        },
        {
            index : 3,
            question : '       ',
            answer : '                      .               .               .                            .                              .                !',
        },
        {
            index : 4,
            question : '       ',
            answer : '                      .               .               .                            .                              .                !',
        },
    ]
    const [click, setClick] = useState();
    const handleClick = (index) => {
        setClick(index)
    }
    return (
        <>
        <div className='all00'>
            <Fade>
            <h1> </h1>
            </Fade>
            <div className='all11'>
                {Data.map((item, index) => {
                    return (
                        <Fade bottom distance="20%">
                        <div className={click === item.index ? 'card00Active' : 'card00'} key={index}>
                            <h3 onClick={() => handleClick(item.index)}>{item.question}<span>{click === item.index ? '-' : '+'}</span></h3>
                            <p>{item.answer}</p>
                        </div>
                        </Fade>
                    )
                })}
            </div>
        </div>
        </>
    )
}

export default FAQ;